<template>
  <section class="news" v-if="this.posts.count != 0">
    <div class="container">
      <h2><span>Últimas</span>Notícias</h2>
      <router-link :to="{ name: 'Blog' }" class="view-more" exact>Ver todas <img alt="Icon arrow left" src="~@/assets/icons/arrow-left.svg"></router-link>
    </div>
    <div class="container container-news">
      <div
        class="box-news"
        v-for="item in this.posts"
        :value="item.id"
        :key="item.id"
      >
        <img class="news-image" alt="" :src="item.attributes.banner_url">
        <h3>{{ item.attributes.title }}</h3>
        <p>{{ item.attributes.description }}</p>
        <router-link
          :to="{
            name: 'BlogPost',
            params: { slug: item.attributes.slug }
          }"
          exact
        >
          Ler mais
        </router-link>
      </div>
    </div>
  </section>
</template>

<script>
import { blogService } from '@/services/blog.service'

export default {
  name: 'News',

  data () {
    return {
      posts: []
    }
  },

  mounted () {
    this.getPosts()
  },

  methods: {
    async getPosts () {
      try {
        const posts = await blogService.listPosts()
        this.posts = posts.data.data.slice(0, 3)
      } catch (error) {
        console.log('Ocorreu um erro inesperado.')
      }
    }

  }
}
</script>

