import Vue from 'vue'

export const propertiesService = {
  listProperties,
  getProperties,
  getBenefits
}

function listProperties (params, pagination) {
  return Vue.axios.get(`v2/public/properties?items=${pagination.perPage}&page=${pagination.page}`, { params })
}

function getProperties (id) {
  return Vue.axios.get(`v2/public/properties/${id}`)
}

function getBenefits () {
  return Vue.axios.get('v2/public/features')
}
