<template>
  <section class="properties-section">
    <div class="container">
      <h2><span>Últimos</span>Lançamentos</h2>
      <router-link :to="{ name: 'PropertiesList' }" class="view-more" exact>Ver todos <img alt="Icon arrow left" src="~@/assets/icons/arrow-left.svg"></router-link>
    </div>
    <div class="container container-properties">
      <div
        class="box-properties"
        v-for="item in this.properties"
        :value="item.id"
        :key="item.id"
      >
        <router-link :to="{ name: 'Properties', params: { slug: item.attributes.slug, id: item.id } }" exact>
          <img class="properties-image" alt="" :src="item.attributes.building.banner_url">
          <div class="properties-content">
            <p class="tag">{{ getStatus(item.attributes.status) }}</p>
            <div>
              <router-link :to="{ name: 'Properties', params: { slug: item.attributes.slug, id: item.id } }" exact>
                <img class="logo" alt="" :src="item.attributes.building.logo_url">
              </router-link>

              <router-link :to="{ name: 'Properties', params: { slug: item.attributes.slug, id: item.id } }" exact>
                <h3>{{ item.attributes.name }}</h3>
              </router-link>

              <router-link :to="{ name: 'Properties', params: { slug: item.attributes.slug, id: item.id } }" exact>
                <p class="locale">{{ item.attributes.address }}</p>
              </router-link>

              <p
                class="infos"
                v-for="features in item.attributes.features.slice(0,2)"
                :value="features.id"
                :key="features.id"
              >
                {{ features.name }}
              </p>
            </div>
          </div>
        </router-link>
      </div>
    </div>
  </section>
</template>

<script>
import { propertiesService } from '@/services/properties.service'

export default {
  name: 'PropertiesSection',
  data () {
    return {
      properties: []
    }
  },

  mounted () {
    this.getProperties()
  },

  methods: {
    async getProperties () {
      try {
        const pagination = {
          perPage: 4,
          page: 1
        }
        const properties = await propertiesService.listProperties({ is_visible: true }, pagination)
        this.properties = properties.data.data.slice(0, 4)
      } catch (error) {
        console.log('Ocorreu um erro inesperado.')
      }
    },

    getStatus (key) {
      var status = '-'
      switch (key) {
        case 'under_construction':
          status = 'Em construção'
          break
        case 'release':
          status = 'Lançamento'
          break
        case 'ready_live':
          status = 'Pronto para morar'
          break
        case 'ready_build':
          status = 'Pronto para construir'
          break
        case 'pre_release':
          status = 'Pré Lançamento'
          break
        case 'done':
          status = 'Pronto'
          break
        case 'sold':
          status = '100% vendido'
          break
      }
      return status
    }

  }
}
</script>

